import React from 'react';
import '../node_modules/bootstrap/dist/css/bootstrap.css';
import web from "../src/images/img2.png";
import {NavLink} from "react-router-dom";

const Common =(props) =>{
    return (
        <>
            <section id="header" className="d-flex align-items-center">
            <div className='container-fluid'>
      <div className='row'>
          <div className='col-10 mx-auto'>
          <div className='row'>

             <div className='col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column'>
               <h1>{props.name} <span className="brand_name">WebStack!</span> </h1>
               <h2 className='my-3'>We Are The Team Of Skilled Developers.</h2>

               <div className='mt-3'>
                  <NavLink to={props.visit} className=' btn btn-primary btn-get-started'>{props.btnname} <i className="fa fa-arrow-circle-right" aria-hidden="true"></i></NavLink>
               </div>
             </div>

             <div className='col-md-6 order-1 order-lg-2 header-img'>
                <img src={props.imgsrc} className='img-fluid animated' alt="Common-img" />
             </div>
             </div>
          </div>
          </div>
          </div>


            </section>
        </>
    )
}


export default Common;